@import "https://maxcdn.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.min.css";
* {
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-weight: bold;
}

body {
  background-image: url("https://images.unsplash.com/photo-1544764200-d834fd210a23?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxjb2xsZWN0aW9uLXBhZ2V8MnxaMUlIT09nX3RVMHx8ZW58MHx8fHw%3D&auto=format&fit=crop&w=500&q=60");
  background-size: cover;
}

.link-home {
  margin-bottom: 0;
}

.tasks {
  justify-content: center;
  align-items: center;
  display: flex;
}

.todo-list {
  width: 25%;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(10px);
  background: linear-gradient(135deg, rgba(0, 138, 251, .62), rgba(112, 150, 232, .07), rgba(51, 0, 255, 0));
  border: 1px solid rgba(225, 225, 225, .18);
  border-radius: 15px;
  padding: 15px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, .17);
}

.header {
  width: 280px;
  background-color: rgba(0, 0, 0, 0);
  margin: auto;
  position: relative;
}

.header__input {
  width: 250px;
  background-color: rgba(255, 255, 255, .84);
  border: none;
  border-bottom: 4px solid #504f4f;
  border-radius: 15px 15px 0 0;
  outline: none;
  padding: 12px;
  transition: all 1s;
}

.header__input::-ms-input-placeholder {
  color: rgba(14, 14, 15, .47);
}

.header__input::placeholder {
  color: rgba(14, 14, 15, .47);
}

.header__input:focus {
  border-bottom: 4px solid #1acbcf !important;
}

.header__button {
  height: 100%;
  width: 35px;
  cursor: pointer;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 5px;
  transition: all 1s;
  position: absolute;
  top: 0;
  right: -5%;
}

.header__button:hover {
  color: #1acbcf;
  border-color: #1acbcf;
}

.list {
  flex-direction: column;
  justify-content: center;
  margin: 0;
  padding: 25px 0;
  list-style: none;
  display: flex;
}

.current-list {
  border-bottom: 2px solid #fff;
}

.list__item {
  width: 90%;
  color: #fff;
  justify-content: space-between;
  font-size: 22px;
  display: flex;
}

.list__item-name {
  width: 235px;
}

.list__button {
  color: #fff;
  cursor: pointer;
  transition: all 1s;
}

.list__button:hover.remove {
  color: #f01938;
}

.list__button:hover.complete, .uncomplete {
  color: #14fb27;
}

.done-list .list__item-name {
  -webkit-text-decoration: line-through #000;
  text-decoration: line-through #000;
  text-decoration-thickness: 5px;
}

